.heroContainer{
display: flex;
justify-content: center;
flex-direction: column;
align-items: center;
height: 100%;
min-height: 600px;
padding: 10px 10px;
 background-size:cover;
 background-color: #3e3e3e73;

 
// object-fit: cover;


>div{
width: 80%;
display: flex;
flex-direction: column;
justify-content: center;
color: white;
text-shadow: 2px 2px #23232394;   
padding: 5px;
margin-left: 85px;


>h3{
    font-size:2.7rem ;
}
>p{
    margin-top: -15px;
    font-size: 1.2rem;
}
}
>div:nth-child(2){
    display: flex;
    margin-bottom: 60px;

    >span{
            display: flex;
            gap: 1em;
            justify-content: center;
            align-items: center;
            background: #f5f5f5;
            max-width: 80%;
            min-height: 60px;
            border-radius: 5px;
        >input{
            
            outline: transparent;
            border: none;
            width: 80%;
            padding: 12px 0px;
            background-color: transparent;
            font-size: medium;
        }
        @media only screen and (max-width: 900px) {


        
            >span{
                display: flex;
                gap: 1em;
                justify-content: center;
                align-items: center;
                background: #f5f5f5;
                max-width: 90%;
                min-height: 60px;
                border-radius: 5px;
            >input{
                
                outline: transparent;
                border: none;
                width: 90%;
                // padding: 12px 0px;
                background-color: transparent;
                font-size: medium;
            }
        
            }
        
        }

    }

    >span:hover{
        outline: 0.2rem solid #9f9f9f71;
    }


}

>div{
>span{
    
    text-align: center;
    color: white;
    text-shadow: 1px 1px black;
} 
}


}