.imageCollection{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1em;
    padding: 10px 20px;
    position: relative;
    background-size: cover;
    
    >div{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    
   > .more{
        padding: 15px;
        font-size: 1rem;
        border-radius: .5em ;
        outline: none;
        font-weight: 600;
        border: none;
        background-color: black;
        color: white;

   
        &:hover {
        background-color: rgb(255, 255, 255);
        color: rgb(0, 0, 0);
        outline: 2px solid black;
        
    }
    }
}
}

.modal-image {
    width: 400px;
    height: auto;
  }
  
  .modal-image-large {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
  }