.navContainer {
display: flex;
padding: 10px 15px;
align-items: center;
justify-content: center;
>img {
    display: flex;
    width: 42px;
    height: 42px;
    align-items: center;
    margin-right: 8px;
  }
 > div{
    flex: 1;
      display: flex;
      border: 1px  #eee;
     border-radius: 50px;
     margin-left: 5px;
     align-items: center;
     padding-left: 15px;
     padding-right: 20px;
     background-color: #eee;
     font-size: 16px;
    > input{
        outline: transparent;
        border: none;
        width: 100%;
    padding: 12px 10px;
    background-color: transparent;
    }
    
    }
    .search:hover{
        outline: 0.1rem solid #9f9f9f71;
    
    }
    >span{
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        >button{
            cursor: pointer;
            background-color: transparent;
            outline: transparent;
            border: 1px solid rgb(204, 203, 203);
            height: 35px;
            font-size:15px;
            user-select: none;
        }
       > button:hover{
            border: 1px solid rgb(149, 149, 149);
        }
    }
   
    @media only screen and (max-width: 900px) {


        .menu {
           display: none;
        
        }
    }


}


.menu{
  
    display: flex;
   gap: 1em;
    align-items: center;
    justify-content:space-between;
    padding-left: 20px;
    margin-left: 10px; 
    margin-right: 10px;
   

}

.menu-item{
    font-size:15px;
    user-select: none;
    color: #979393;
}
.menu-item:nth-child(3){
    background: #121FCF;
background: linear-gradient(to right, #121FCF 0%, #CF1512 100%);
background-clip: text;
-webkit-text-fill-color: transparent;
}
hr{
height: 4.5vh;
background-color:  #4a4a4a;
 
}

.menu-item:hover{
    color: #4a4a4a;
    cursor: pointer;
}

